* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #fdfdfd;
  color: #333;
  line-height: 1.6;
}

/* Container */
#content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* Navigation Bar */
nav {
  display: flex;
  justify-content: center;
  background-color: #ffffff;
  border-bottom: 1px solid #ddd;
  padding: 15px 0;
  position: sticky;
  top: 0;
  z-index: 100;
  flex-wrap: wrap;
  gap: 10px;
}

nav button {
  background: none;
  border: 1px solid #2e8b57;
  color: #2e8b57;
  padding: 10px 20px;
  margin: 0 5px;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

nav button:hover {
  background-color: #2e8b57;
  color: white;
}

/* Active Button */
.buttonClicked {
  background-color: #2e8b57;
  color: white;
  border-radius: 5px;
  padding: 8px 14px;
  border: none;
}

/* Responsive Images */
img {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Footer */
footer {
  text-align: center;
  font-size: 0.9rem;
  color: #666;
  padding: 20px 10px;
  border-top: 1px solid #ddd;
  background-color: #f9f9f9;
  margin-top: 40px;
}

/* ========== MEDIA QUERIES ========== */

/* Mobile Phones */
@media (max-width: 600px) {
  nav {
    flex-direction: column;
    align-items: center;
  }

  nav button {
    width: 90%;
    margin: 5px 0;
    font-size: 1rem;
  }

  #content {
    padding: 10px;
  }

  footer {
    font-size: 0.8rem;
  }
}

/* Tablets */
@media (min-width: 601px) and (max-width: 900px) {
  nav button {
    padding: 8px 16px;
    font-size: 0.95rem;
  }

  #content {
    padding: 15px;
  }
}

/* Large Screens */
@media (min-width: 1200px) {
  nav {
    padding: 20px 0;
  }

  nav button {
    font-size: 1.1rem;
    padding: 12px 24px;
  }

  footer {
    font-size: 1rem;
  }
}
